<template>
  <div class="layout">
    <group>
      <x-input title="ID" type="text" placeholder="" :value.sync="id" @on-change="inputId"></x-input>
    </group>
    <group>
      <x-input title="&nbsp;" type="password" placeholder="" :value.sync="password" @on-change="inputPassword"></x-input>
    </group>
    <flexbox class="buttons">
     <x-button type="primary" @click="login"></x-button>
    </flexbox>
    <toast :show.sync="show" type="cancel" :time="1000"></toast>
  </div>
</template>

<script>
import { XButton, Flexbox, Group, XInput, Toast } from 'vux/src/components';
import { store } from '../data/user';
export default {
  components: {
    XButton,
    Flexbox,
    XInput,
    Group,
    Toast,
  },
  data() {
    return {
      id: null,
      password: null,
    };
  },
  methods: {
    login() {
      const { id, password } = this;
      this.$http.post('/api/login', { id, password }).then(resp => {
        const resault = resp.json();
        if (resault.success) {
          const { company } = resault;
          store.set('COMPANY', company);
          this.$router.go({ name: 'member_order' });
        } else {
          this.$set('show', true);
        }
      });
    },
    inputId(val) {
      console.log(val);
    },
    inputPassword(val) {
      console.log(val);
    },
  },
};
</script>

<style scoped>
  .layout {
    height: 100%;
    width: 100%;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
  }
  .buttons {
    width: 90%;
    position: absolute;
    bottom: 20px;
    left: 5%;
  }
</style>



