import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
  isLoading: false,
  direction: 'forward',
};

/* eslint-disable no-shadow, no-param-reassign */
export default new Vuex.Store({
  state,
  mutations: {
    UPDATE_LOADING(state, status) {
      state.isLoading = status;
    },
    UPDATE_DIRECTION(state, direction) {
      state.direction = direction;
    },
  },
});



